header {
    width: 100%;
    height: 100vh;
    position: relative;

    #topbar {
        position: fixed;
        top: 0;
        left: 0;
        background-color: #000;
        color: #fff;
        padding: 25px 0;
        z-index: 10000;
    }

    #topslider {
        width: 100%;
        height: 100%;
        background-color: brown;

        .carousel, .carousel-inner, .carousel-item {
            height: 100%;
        }
    }
}